ul {
  list-style: none;
  padding: 0;
  margin: 0;
}


.burger-btn__close-icon {
    margin-right: 20px;
    margin-bottom: 20.26px;
}


.burger-btn {

    @media screen and (max-width: 767px) {
        font-family: $font-main;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.31;
        letter-spacing: .04em;
        transition: .25s cubic-bezier(.4, 0, .2, 1);
        color: $color-boss;
        // width: 186px;
        height: 44px;
        margin: 60px 21px 19px 20px;
        padding-left: 48.64px;
        padding-right: 48.64px;
        background: #fff;
        border: transparent;
        border-radius: 22px;

        @media screen and (min-width: 480px) and (max-width: 767px) {
            margin-top: 58px;
            margin-bottom: 20px;
            margin-left: 34px;
        }
    }


    
    @media screen and (min-width: 480px) and (max-width: 767px) {
        // margin: 60px 34px 19px;
        padding-left: 43.28px;
        padding-right: 43.28px;
    }

    @media screen and (min-width: 768px) {
        display: none;
    }

}

.burger-btn__arrow-icon {
    margin-left: 15.77px;
}


.mobile-menu {
  display: none;
}


.burger-menu-btn {
    @media screen and (min-width: 768px){
        display: none;
    }
}

.modal-toggle__close {
  position: absolute;
  right: 28px;
  top: 23px;
  padding: 0;
  border: none;
  background-color: inherit;
  cursor: pointer;

  @media (min-width: 768px) {
    right: 34px;
  }
}

