.modal-toggle.is-open {
  transform: translateX(0);
}

.modal-toggle {
    
    @media screen and (max-width: 479px) {
        position: fixed;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: 50px;
        background-color: #e17992;
        z-index: 9999;
        transform: translateX(100%);
        transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 480px) and ( max-width: 767px) {
        position: fixed;
        top: 0;
        right: 0;
        width: 254px;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: 50px;
        background-color: #e17992;
        z-index: 9999;
        transform: translateX(100%);
        transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    }
  
}

.link {
  text-decoration: none;
  color: inherit;
}

.burger-link {
  margin: 24px;
  text-align: center;
  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        color: $color-boss;
    }
}

.burger__item {
  width: 250px;
  height: 42px;
  border-top: 1px solid #e18298;
}

.burger__item,
.burger__nav {
  display: flex;
  align-items: center;
}

.burger__nav {
    

    @media screen and (max-width: 767px) {
        font-family: DM Sans, sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.04em;
        color: #fff;
        border-bottom: 1px solid #e18298;
    }

    @media screen and (min-width: 768px) {
        display: none;
    }
  
}





